/* src/components/Portfolio.css */

.portfolio {
    padding: 60px 0;
    text-align: center;
    background-color: #fff;
}

.portfolio h2 {
    font-size: 2.5em;
    margin-bottom: 30px;
    font-weight: 700;
}

.portfolio-item {
    display: inline-block;
    width: 45%;
    margin: 20px;
    background-color: #fafafa;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
}

.portfolio-item img {
    width: 100%;
    height: auto;
    border-radius: 10px;
}

.portfolio-item h3 {
    font-size: 1.6em;
    margin-top: 15px;
}

.portfolio-item p {
    font-size: 1.1em;
    color: #666;
}

.portfolio-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}
