/* General section styles */
.about {
    padding: 60px 20px;
    background-color: #f8f9fa;
    color: #333;
}

.about-content {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

/* About Us Header */
.about-header {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #2c3e50;
}

.intro {
    font-size: 18px;
    margin: 20px auto;
    max-width: 800px;
    line-height: 1.6;
    color: #555;
}

/* Mission Section */
.mission {
    margin-top: 40px;
}

.mission h3 {
    font-size: 28px;
    color: #2c3e50;
    margin-bottom: 20px;
}

.mission p {
    font-size: 18px;
    max-width: 900px;
    margin: 0 auto;
    line-height: 1.6;
    color: #555;
}

/* Team Section */
.team {
    margin-top: 60px;
}

.team h3 {
    font-size: 28px;
    color: #2c3e50;
    margin-bottom: 20px;
}

.team-members {
    display: flex;
    justify-content: center;
    gap: 40px;
    flex-wrap: wrap;
}

.team-member {
    text-align: center;
    width: 250px;
}

.team-img img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-bottom: 15px;
    border: 4px solid #3498db;
    transition: transform 0.3s ease-in-out;
}

.team-img img:hover {
    transform: scale(1.1);
}

.team-member h4 {
    font-size: 20px;
    font-weight: bold;
    color: #2c3e50;
}

.team-member p {
    font-size: 16px;
    color: #7f8c8d;
}

/* Responsive design */
@media (max-width: 768px) {
    .team-members {
        flex-direction: column;
        align-items: center;
    }
}
