/* General Header Styling */
header {
    display: flex;  /* Flexbox container */
    justify-content: space-between;  /* Space between the logo and navigation */
    align-items: center;  /* Vertically align the logo and links */
    background-color: #282c34;  /* Dark background color */
    padding: 10px 40px;  /* Padding for space around the content */
    color: white;  /* White text for contrast */
  }
  
  /* Logo Styling */
  header .logo {
    font-size: 2rem;
    font-weight: bold;
    color: #fff;  /* White color for logo */
  }
  
  /* Navbar Styling */
  .navbar {
    display: flex;  /* Flex container for navbar */
    justify-content: flex-end;  /* Align the navbar items to the right */
  }
  
  .navbar-links {
    display: flex;  /* Flexbox for horizontal navigation */
    list-style: none;  /* Remove bullet points */
  }
  
  .navbar-links li {
    margin: 0 15px;  /* Space between each menu item */
  }
  
  .navbar-links li a {
    color: white;
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;
    padding: 10px 20px;  /* Padding for clickable area */
    transition: background-color 0.3s ease, color 0.3s ease;  /* Smooth transitions */
  }
  
  .navbar-links li a:hover {
    background-color: #61dafb;  /* Light blue background on hover */
    color: #282c34;  /* Dark text color on hover */
  }
  
  /* Mobile Responsiveness */
  @media (max-width: 768px) {
    header {
      flex-direction: column;  /* Stack header elements vertically on smaller screens */
      text-align: center;  /* Center text for better mobile layout */
    }
  
    .navbar-links {
      flex-direction: column;  /* Stack navbar items vertically */
      margin-top: 20px;  /* Add space between logo and navbar */
    }
  
    .navbar-links li {
      margin-bottom: 10px;  /* Space between links in mobile view */
    }
  
    .navbar-links li a {
      padding: 12px;  /* Increase padding for mobile-friendly links */
    }
  }
  