.contact-section {
    padding: 2rem;
    background-color: #f9f9f9;
}

.contact-container {
    max-width: 600px;
    margin: 0 auto;
    background: white;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.contact-title {
    text-align: center;
    margin-bottom: 1rem;
    font-size: 1.8rem;
    color: #333;
}

.contact-description {
    text-align: center;
    margin-bottom: 2rem;
    font-size: 1rem;
    color: #666;
}

.contact-form .form-group {
    margin-bottom: 1rem;
}

.contact-form label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
    color: #555;
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 0.8rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
    outline: none;
    transition: border-color 0.3s ease;
}

.contact-form input:focus,
.contact-form textarea:focus {
    border-color: #007bff;
}

.contact-form .submit-button {
    display: inline-block;
    width: 100%;
    padding: 0.8rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.contact-form .submit-button:hover {
    background-color: #0056b3;
}

.feedback {
    margin-top: 1rem;
    padding: 1rem;
    border-radius: 4px;
    font-size: 0.9rem;
}

.success-message {
    background-color: #d4edda;
    color: #155724;
    border: 1px solid #c3e6cb;
}

.error-message {
    background-color: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
}

.status-message {
    background-color: #fff3cd;
    color: #856404;
    border: 1px solid #ffeeba;
}
